<template>

    <mt-dialog 
        width="70%"
        @handl-close="handleClose"
    >
        <div class="preview">
            <div style="width:100%;" v-html="content"></div>
        </div>
        <div slot="foot">
            <el-button type="primary" @click="handleClose">确定</el-button>
        </div>
    </mt-dialog>
</template>

<script>
export default {
    data(){
        return {
            content:'',
            dialogVisible:true
        }
    },
    methods:{
        handleClose(){
            this.dialogVisible = false;
            this.$router.go(-1);
        }
    },
    created() {
        if(this.$route.params.content){
            this.content = this.$route.params.content;
        }else{
            this.handleClose();
        }
    },
}
</script>

<style lang="scss" scoped>
    
    .preview{
        position: relative;
        bottom: 0;
        top: 0;
        background: white;
        width: 100%;
        text-align: left;
        padding: 5px;
        overflow: scroll;
    }
</style>
